.Playboard {
    display: grid;
    grid-template-columns: .8fr .8fr .8fr;
    grid-template-rows: .8fr .8fr .8fr;
    justify-content: center;
    align-content: center;
    /* padding-left: 1rem;
    padding-top: .5rem; */
    /* max-width: 99vw; */
    height: 420px;
    max-height: 420px;
    overflow:auto;
}

.Landscape {
    display: grid;
    /* justify-self: center; */
    align-items: center;
    /* max-width: 90%; */
    outline: dotted #c3602f 1px;
}

.Playboard button {
    padding: 10px;
    background-color: #2bb673;
}
.newBtn {
    display: grid;
    grid-column: 2;
    grid-row: 2;
    justify-self: center;
}

@media (max-width: 599px){
    .Playboard {
        grid-template-columns: .5fr .5fr .5fr;
        grid-template-rows: .5fr .5fr .5fr;
        height: 283px;
        max-height: 283px;
    }
}
